@import url(https://fonts.googleapis.com/css2?family=Averia+Serif+Libre);
html {
  height: 100vh;
}

body {
  margin: 0;
  height: 100%;
  background-color: #F7F7F7;
}

#root {
  height: 100%;
}
.App {
  color: #15493c;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 2fr 15fr 1fr;
  grid-template-areas:
    "header"
    "main"
    "footer";
}

header {
  grid-area: header;
}

main {
  grid-area: main;
}

footer {
  grid-area: footer;
}

.main {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Averia Serif Libre";
  text-align: center;
}

.headline {
  font-size: 2rem;
}

.hover-states:hover {
  cursor: pointer;
  transform: scale(1.03);
}

a {
  color: #15493c;
  margin: 1rem;
}

.heisenberg-background {
  position: absolute;
  margin-left: -300px;
  margin-top: 100px;
  height: 600px;
  z-index: -10;
  opacity: 0.3;
}

a:visited {
  color: #106924;
}

@media screen and (max-width: 850px) {
  .headline {
    margin: 0;
  }
}

@media screen and (max-width: 400px) {
  .heisenberg-background {
    margin-left: -400px;
  }
}

.header {
  color: #15493C;
  font-family: 'Averia Serif Libre';
  border-bottom: 3px solid #15493C;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 2.5rem;
  margin: 1rem;
}

.logo-block {
  color: white;
  font-family: 'Helvetica';
  background: linear-gradient(to bottom right, #15493C, #106924);
  border: 2px solid; 
  border-color: #15493C #106924 #106924  #15493C ;
}

.logo-block:nth-of-type(2) {
  margin-left: 58px;
}

.home-link {
  align-self: flex-end;
  margin: 1rem;
}

.rules-container {
  font-size: 20px;
}

.knock-button {
  border: none;
  border-radius: 3px;
  background-image: linear-gradient(#106924, #15493C);
  height: 40px;
  width: 180px;
  color: white;
  font-size: 25px;
  font-family: 'Averia Serif Libre';
  transition: all .2s ease-in-out; 
}
.card {
  height: 5rem;
  width: 300px;
  border: 8px solid #15493C;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
}

.card:hover {
  box-shadow: 1px 1px 50px #106924;
}



 

.cards-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  align-items: center;
}
.quote-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
}

.quote {
  margin-top: 0;
}
.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-row-gap: 0.8rem;
  row-gap: 0.8rem;
}

.dev {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-row-gap: 0.5rem;
  row-gap: 0.5rem;
}

.about * {
  margin: 0;
}

.dev-image {
  height: 200px;
}

.why {
  width: 500px;
}

ul {
  list-style-type: none;
  padding: 0;
}

.footer {
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-end;
}


